<template>
  <div class="bigbox">
    <div>
      <span class="span2"
            :style="{color: $vuetify.theme.defaults.light.primary}"
            v-if="readonly"
            @click="addvariate">{{$t('label_Join_the_variables')}}
        <mrc_ess_svgIcon iconClass='open_in_new'></mrc_ess_svgIcon>
      </span>
    </div>
    <mrc_ess_myDialog v-model="dialog"
                      :title="$t('label_A_new_level')"
                      width='800'
                      hiddenTitle
                      :disabled='false'
                      @cancel='close'>
      <v-card-text>
        <v-tabs v-model="tab"
                grow
                background-color="#ffffff"
                color='primary'
                dark>
          <v-tab v-for="item in items"
                 style="color:black"
                 :key="item.tab">
            {{ item.tab }}
          </v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab">
          <v-tab-item style="overflow-y: scroll;max-height: 300px">
            <div v-for="(item,index) in variatelist"
                 :key="index+item">
              <div class="ptbox"
                   @click="changebgd(item,index)"
                   :class="item.divbgd?'divbgd':''">
                <span>{{item.property}}</span>
                <span style='color:#bcbcbc'>{{item.name}}</span>
              </div>
            </div>
          </v-tab-item>

          <v-tab-item style="overflow-y: scroll;max-height: 300px">
            <div v-for="(item,index) in syslist"
                 :key="index+item">
              <div class="ptbox"
                   @click="changebgd2(item,index)"
                   :class="item.divbgd?'divbgd':''">
                <span>{{item.property}}</span>
                <span style='color:#bcbcbc'>{{item.name}}</span>
              </div>
            </div>
          </v-tab-item>

        </v-tabs-items>
      </v-card-text>
    </mrc_ess_myDialog>
