<script>
import { getBusinessList } from '@/service/api/prompt.js'
export default {
  components: {},
  props: {
    readonly: {
      type: Boolean,
      default: true
    },
    function_uuid: {
      type: String
    },
    updateFunction_uuid: {
      type: String
    },
  },
  data () {
    return {
      itemss: [
      ],
      offset: true,
      tab: null,
      items: [
        { tab: '功能变量' },
        { tab: '系统变量' },
      ],
      dialog: false,
      variatelist: [],
      syslist: []
    };
  },
  computed: {},
  created () { },
  watch: {
  },
  mounted () {
    // document.addEventListener('click', (e) => {
    //   if (!this.$el.contains(e.target)) {
    //     if (this.dialog) {
    //       this.close()
    //     }
    //   }
    // })
  },

  methods: {
    async getvariatelist () { //获取属性字段列表
      //发送请求)
      if (!this.variatelist.length > 0) {
        let res = null
        if (this.$store.state.uuid) {
          res = await getBusinessList({
            prompt_name: 'email_address_field',
            function_uuid: this.updateFunction_uuid
          })
        }
        else {
          res = await getBusinessList({
            prompt_name: 'email_address_field',
            function_uuid: this.function_uuid
          })
        }
        this.variatelist = res.data.map(item => {
          item.property = '{{' + item.field_name + '}}'
          delete item.field_name
          return item
        })
      }
      this.variatelist = this.variatelist.map(item => {
        return { ...item, divbgd: false }
      })
    },
    getsyslist () { //获取系统变量
      //发送请求)
      this.syslist = [ //系统变量
        {
          property: '{{112saaas}}',
          name: 'xasxaxaaxa'
        },
        {
