        },
        {
          property: '{{112saaas}}',
          name: 'xasxaxaaxa'
        },
        {
          property: '{{112xasaokhhh}}',
          name: 'xasxaxaaxa'
        },
        {
          property: '{{112qqqws}}',
          name: 'xasxaxaxaxaaxa'
        },
        {
          property: '{{112qwertyui}}',
          name: 'xaxxsxaxxaaxa'
        },
        {
          property: '{{112saaas}}',
          name: 'xasxaxaaxa'
        },
        {
          property: '{{112xasaokhhh}}',
          name: 'xasxaxaaxa'
        },
        {
          property: '{{112qqqws}}',
          name: 'xasxaxaxaxaaxa'
        },
        {
          property: '{{112qwertyui}}',
          name: 'xaxxsxaxxaaxa'
        }
      ]
      this.syslist = this.variatelist.map(item => {
        return { ...item, divbgd: false }
      })
    },
    async addvariate () {
      this.dialog = true
      await this.getvariatelist()
      this.getsyslist()
    },
    changebgd (item, index) {
      item.divbgd = !item.divbgd
      this.$set(this.variatelist, index, item)
    },
    changebgd2 (item, index) {
      item.divbgd = !item.divbgd
      this.$set(this.syslist, index, item)
    },
    close () {
      this.dialog = false
      let newpropertylist = []
      this.variatelist.map(item => {
        if (item.divbgd == true) {
          newpropertylist.push(item)
        }
      })
      this.syslist.map(item => {
        if (item.divbgd == true) {
          newpropertylist.push(item)
        }
      })
      this.$emit('getpropertylist', newpropertylist)
      this.variatelist = this.variatelist.map(item => {
        return { ...item, divbgd: false }
      })
      this.syslist = this.syslist.map(item => {
        return { ...item, divbgd: false }
      })
    },
    save () {
      // this.$v.editedItem.$touch()
