<template>
  <div>
    <v-menu offset-y
            v-model="isShow"
            :close-on-content-click='false'>
      <template v-slot:activator="{ on, attrs }">
        <div>
          <mrc_ess_input v-if="!isShow"
                         v-bind="attrs"
                         v-on="on"
                         :label="label || $t('label_Email_message')"
                         :readonly='readonly'
                         v-model="defaultemailsubject"
                         append-icon="mdi-longitude">
          </mrc_ess_input>
          <h4 v-else
              :style="{color: $vuetify.theme.defaults.light.primary}">{{label}}</h4>
        </div>
      </template>
      <v-list class="bigbox">
        <v-list-item v-for="(item, index) in childrenpurport"
                     :key="index">
          <mrc_ess_input :label="$t(`label.${item.language_code}`)+'*'"
                         :rules='rules'
                         @blur="handleinputBlur"
                         :readonly='readonly'
                         v-model="item.content">
          </mrc_ess_input>
          <propertyDialog :readonly='!readonly'
                          :function_uuid='function_uuid'
                          :updateFunction_uuid='updateFunction_uuid'
                          @getpropertylist='getpropertylist($event,index)'></propertyDialog>
        </v-list-item>
        <div class="btnbox"
             v-show="!readonly">
          <mrc_ess_submitBtn @cancel='cancel'
                             @success='success'
                             cancelText='cancel'
                             successText='OK'></mrc_ess_submitBtn>
        </div>
      </v-list>
    </v-menu>
  </div>
</template>

<script>
import propertyDialog from './propertyDialog'
export default {
  components: {
    propertyDialog
  },
  props: {
    label: {
      type: String
    },
    purport: {
      type: Array,
      default: () => []
    },
    defaultemailsubject: {
      type: String,
      default: '请设置内容'
    },
    readonly: {
      type: Boolean,
      default: false
    },
    function_uuid: {
      type: String
    },
    updateFunction_uuid: {
      type: String
    },
  },
  model: {
    prop: 'purport',
    event: 'purportChange'
  },
  data () {
    return {
      rules: [
        value => !!value || 'Required.',
      ],
      isShow: false,
      childrenpurport: [],
      blurIndex: null
    };
  },
  computed: {},
  created () { },
  watch: {
    purport: {
      handler (v) {
        this.childrenpurport = this.$clone(v)
      }, deep: true
    }
  },
  mounted () {
    this.childrenpurport = this.$clone(this.purport)
  },

  methods: {
    success () {
      this.isShow = false
      this.$emit('purportChange', this.childrenpurport)
    },
    cancel () {
      this.isShow = false
      this.childrenpurport = this.$clone(this.purport)
    },
    getpropertylist (v, index) {
      v.map(item1 => {
        this.childrenpurport[index].content ? this.childrenpurport[index].content = this.childrenpurport[index].content.slice(0, this.blurIndex) + item1.property + this.childrenpurport[index].content.slice(this.blurIndex) : this.childrenpurport[index].content = item1.property
      })
    },
    handleinputBlur (e) {
      this.blurIndex = e.srcElement.selectionStart
    }
  },
  validations: {

  }
};
</script>

<style scoped>
.bigbox {
  padding: 0;
}
.btnbox /deep/ .btnGroup {
  justify-content: initial;
  flex-direction: row-reverse;
  padding-right: 35px;
}
.bigbox /deep/ .v-list-item {
  display: block;
  margin-bottom: 15px;
}
.bigbox /deep/ .v-.v-messages {
  min-height: 0px;
}
</style>
